import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import ReportLink from "../../components/report-link"
import Seo from "../../components/seo"
import "../../styles/main-archives.css"

export default function Home({ data }) {
  const description = `やさしいDX推進協議会の活動の様子をご紹介いたします。| 小規模事業者のデジタル化・IT活用サポート HOTSOUL`
  const pageUrl = `https://www.hotsoul.jp/dx-council/articles/`

  return(
    <Layout>
      <Seo title="やさしいDX推進協議会 活動報告" description={description} pageUrl={pageUrl} />
      <div className="wrap">
        <section className="archives">
          <h2>やさしいDX推進協議会 活動報告</h2>
          <div className="archives-inner">
            <div className="flex">
              <div className="flex-item">
                <figure>
                  <StaticImage
                    src="../../images/main-dx-council/ph1.jpg"
                    alt="活動報告 第21回 定例会"
                    placeholder="blurred"
                  />
                </figure>
                <p className="date">
                  2021.7.9
                </p>
                <p className="title">
                  【定例会】<br/>
                  第21回 定例会
                </p>
                <p className="txt">
                  ハマポートにて初のリアル開催！をしました。
                </p>
                <div className="archives-btn">
                  <Link to={`/dx-council/first-real-meeting`}>記事を読む</Link>
                </div>
              </div>
              {data.allContentfulReport.edges.map(edge =>
                <ReportLink key={edge.node.slug} article={edge.node} />
              )}
            </div>
          </div>
        </section>
      </div>

    </Layout>
  )
}

export const query = graphql`
  query ListReports {
    allContentfulReport (sort: {fields: updatedAt, order: ASC}) {
      edges {
        node {
          id
          image1 {
            gatsbyImageData(placeholder: BLURRED)
          }
          updatedAt(formatString: "YYYY.M.D")
          title
          slug
          category
          outline
        }
      }
    }
  }
`