import React from "react"
import { Link } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

export default function ReportLink({ article }) {
  const { category, title, updatedAt, outline } = article
  const pageLink = `/dx-council/${article.slug}`
  const image = getImage(article.image1)

  return (
    <div className="flex-item">
      <figure>
        <GatsbyImage image={image} alt={title}/>
      </figure>
      <p className="date">
        {updatedAt}
      </p>
      <p className="title">
        【{category}】
        <br/>
        {title}
      </p>
      <p className="txt">
        {outline}
      </p>
      <div className="archives-btn">
        <Link to={pageLink}>記事を読む</Link>
      </div>
    </div>
  )
}